import React from "react";
import styles from "./OrderDetails.module.css";
import donePic from "../../images/graphics.png";
import {useAppSelector} from "../../utils/hooks";

function OrderDetails() {
  const {order} = useAppSelector(state => state.order)

  return (
    <div className={styles.container}>
      {order.number ? <p
          className={styles.glow + " text text_type_digits-large mt-30"}>{order.number}</p> :
        <p className="mt-15 text text_type_main-default">...</p>}

      <p className="mt-8 text text_type_main-medium"> </p>
      <img className="mt-15" alt=" " src={donePic}/>
      <p className="mt-15 text text_type_main-small">   </p>
      <p className="mt-2 mb-30 text text_type_main-small text_color_inactive">   
        </p>


    </div>
  )
}

export default OrderDetails;